    this.role = this.$store.state.clientAppDecodedToken.Role;
  },
  props: {"orderId":String,"isReview":Boolean},
  mounted() {
    this.getPackageTypes();
    axios
      .get(
        process.env.VUE_APP_OrderMgmtApisBaseUrl +
          "/Orders?orderId=" +
          this.orderId
      )
      .then((response) => {
        this.order = response.data;
        var orderItems = [];
        this.order.orderItems.map((ord) => {
            var element = {
                productType: ord.productName,
                productName: ord.brandProductName,
                sku: ord.stockKeepingUnitName,
                supplierName: ord.supplierName,
                reelSize: ord.reelSize,
                NoOfReels: ord.noOfCoils,
                totalStamps: ord.noOfStamps,
                stampPriceInGHC: ord.stampPrice,
                totalGHC: ord.totalPrice,
                fullfilledPackages: ord.fullfilledPackages,
                _showDetails: false
            }
            orderItems.push(element);
        });
        this.orderItems = [...orderItems];
      });
  },
