  methods: {
    isNtlUser: function () {
      return (
        this.role == roles.TsspAdmin ||
        this.role == roles.TsspIntermediate ||
        this.role == roles.TsspWarehouseIncharge
      );
    },
    isTaxAuthUser: function () {
      return (
        this.role == roles.TaxAuthAdmin ||
        this.role == roles.TaxAuthRevenueOfficer
      );
    },
    redirectToHome() {
      if (this.isNtlUser() || this.isTaxAuthUser()) {
        switch (this.order.orgType) {
          case 4:
            this.$router.push({ name: "StockOrders" });
            break;
          default:
            this.$router.push({ name: "Orders" });
            break;
