        console.log("this.order.orderItems",this.orderItems);
        if(this.selectedRowIndex == index) {
            this.orderItems[index]._showDetails = !this.orderItems[index]._showDetails;
        }
        else {
            this.selectedRowIndex = index;
            this.orderItems.map((item) => {
                item._showDetails = false;
            });
            this.orderItems[index]._showDetails = true;
        }
        console.log("this.orderItems",this.orderItems);
